body {
  --main-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header__content {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.header__logoBox {
  width: 40px;
  height: 40px;
}

.header__nav {
  margin: 0;
}

.header__navList {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header__navItem {
  margin-left: 20px;
}

.header__navItem:first-child {
  margin-left: 0;
}

.navLink {
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.navLink:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

